:root {
  ---fontAndInputBg_Color: white;
  ---dark_Background: linear-gradient(to right, #434343 0%, black 100%);
}

.darkBackground {
    background-image: var(---dark_Background) !important;
    color: white !important;
}
  
.darkButtom {
  margin-left: auto !important;
  background-image: var(---dark_Background) !important;
}

.darkShadow {
  box-shadow: rgb(157, 157, 157) 13px 0.5px 11px -5.5px !important;
}

.darkShadow2 {
  color: var(---fontAndInputBg_Color) !important;
  box-shadow: rgb(157, 157, 157) 8px 3.5px 11px 0px !important;
}